import BaseSubheading from "./BaseSubheading";

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('BaseCardSection', component.exports)
}
export default {
  name: "BaseCardSection",
  components: {
    "base-subheading": BaseSubheading,
  },
  props: {
    title: {
      type: String,
      default: "",
    },
    variation: {
      type: String,
      default: "",
      validator: function(value) {
        return ["", "subdued"].includes(value);
      }
    }
  },
  computed: {
    cardSectionClasses() {
      return {
        "base-card__section--subdued": this.variation === "subdued",
      }
    }
  }
}
