if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('BaseSubheading', component.exports)
}
export default {
  name: "BaseSubheading",
  inheritAttrs: false,
  props: {
    element: {
      type: String,
      default: "h3",
      validator: function(value) {
        return ["h1", "h2", "h3", "h4", "h5", "h6", "p"].includes(value);
      }
    }
  }
}
